.category-buttons{
    display:flex;
    /* flex-wrap: wrap; */
    gap: 6em;
    padding-top: 25px;
    padding-bottom: 10px;;
    padding-left:110px;
    text-align: center;
    color: #000;
    text-align: center;
    font-family: Rasa;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    /* border: solid 3px rebeccapurple; */
}

.category-button{
    width: 160px;
    height: 48px;
    flex-shrink: 0;
    padding-right:2px;
    padding-left:2px;
    border-radius: 15px;
    background-color:#F6963E;


}
.category-button-selected{
    width: 160px;
    height: 48px;
    flex-shrink: 0;
    padding-right:2px;
    padding-left:2px;
    border-radius: 15px;
    /*background-color: papayawhip;*/
    cursor:pointer;


}
.category-button:hover {
    background-color:#F53333;
    transition: 0.7s;
    cursor:pointer;
}
