.header-dropdown {
    position: relative;
}

.categories-button {
    background-color: var(--primary-color-dark);
    margin-top: 1em;
    cursor: pointer;
}

.header-dropdown ul {
    background-color: white;
    color: black;
    display: none;
    /*border-radius: 20px;*/
}

.header-dropdown li {
    padding: 0.25em 0.5em;
}

.header-dropdown a {
    color: black;
    text-decoration: none;
}

.header-dropdown li:hover {
    background: lightgray;
}

.header-dropdown:hover ul {
    display: block;
    position: absolute;
    /* margin-left: 90px; */
    min-width: 8em;
    z-index: 1;
    cursor: pointer;
}

.category-drop-button{
    width: 130px;
    height: 36px;
    flex-shrink: 0;
    background-color: #FCF4D7;
    border-radius: 20px;
    cursor:pointer;
}
.category-drop-button:hover{
    background-color:darkorange;
    transition: 0.7s;
    cursor:pointer;
}
