footer {
    /* background-color: var(--secondary-background-color); */
    background-color: #DBAB00;
    display: flex;
    flex-direction: row;
    /* margin-top: 90px; */
    gap: 1em;
    width: 1200px;
    height: 130px;
    justify-content: space-between;
    font-family:'Rasa';


}

.follow-us-text{
    font-family: 'Rasa';
    /* font-size: 10px; */
}
.links,
.social-media-icons{
    display: flex;
    flex-direction: row;
}

.links,
.social-media-icons:hover{
    /*background-color:#F53333;*/
    transition: 0.7s;
    cursor:pointer;
}

.links{
    gap: 1em;
    padding-top: 18px;
    align-items: center;
}
.links:hover{
    cursor:pointer;
    /*color: #F53333;*/

}
.follow-us-text{
    display:flex;
    flex-direction: column;
}
.follow-text{
    padding-top: 15px;
    padding-right: -100px;
    padding-left: 10px;
    padding-bottom:2px;

}
.social-media-icons {
    padding-top:7px;
    padding-right:30px;
    gap: 1em;
    cursor:pointer;
    /* margin-left: 900px;
    margin-top: 20px; */

}

