.home-page {
    display: flex;
    flex-direction: column;
    color:black;

}

.welcome-text {
    width: 20em;
}

.bestseller-image {
    /*transition: width 2s, height 2s, transform 2s;*/
    /*transition: slide, transform 4s;*/
    /*transition-delay: 1s;*/
    /*transform: rotate(180deg);*/
    /*animation-name: fadeInOut, moveLeft300px, bounce;*/
    /*animation-duration: 10s;*/
    /*animation-iteration-count: infinite;*/
    /*animation-direction: alternate;*/
    /*transition-property: width;*/
    /*transition-duration: 2s;*/
    /*transition-timing-function: linear;*/
    /*transition-delay: 1s;*/
    transform: translateY(100%);
    animation: imageUp 1s ease-out forwards;
    opacity: 0;
}
@keyframes imageUp {
    to{
        transform: translateY(0);
        opacity:1;
    }

}
.bestseller-image-items {
    padding-top:30px;
    padding-left: 95px;
    padding-right: -50px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 2.5em;
}
.buynow{
    padding-left: 40px;
    padding-bottom: 35px;
    padding-top: 10px;
    color: #000;
    text-align: center;
    font-family: Rasa;
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.buy-now-button{
    width: 194px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 20px;
    margin-bottom: 2px;
    color:black;
    background-color: #F6963E;
}

.buy-now-button:hover {
    background-color: red;
    transition: 0.7s;
    cursor:pointer;
}