header {
    background-color: #DBAB00;
    display: flex;
    flex-direction: row;
    padding: 1em;
    gap: 1em;
    width: 1200px;
    height: 130px;
}

.text-logo {
    font-family: var(--title-font-family);
}

.search-bar {
    padding: 0.5em;
}




@media (max-width: 600px) {
    header {
        flex-direction: column;
    }
}
.button{
    background-color:#FCF4D7;
    margin-left:200px;
    margin-top: 100px;
    width: 120px;
    height: 28px;
    flex-shrink: 0;

}
/* .book-store-logo{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1em;
} */
.img-logo{
    width: 140px;
    height: 111px;
    border-radius: 160px;

}
.name-logo{
    width: 177px;
    height: 55px;
    flex-shrink: 0;
    position:relative;
    margin-left:120px;
    margin-top: -80px;

}
.login{
    margin-top:70px;
    margin-right:500px;
    margin-bottom: 120px;

}
.search input[type=text]{
    width:450px;
    height:25px;
    border-radius:100px;
    border: none;
}
.login-cart{
    gap: 2em;
    margin-left:-250px;
    margin-top:-12.5px;
}
.login-button{
    width: 120px;
    height: 36px;
    flex-shrink: 0;
    background-color: #FCF4D7;
    border-radius: 20px;
    margin-bottom: 2px;
    cursor:pointer;
}
.login-button:hover{
    background-color:darkorange;
    transition: 0.7s;
    cursor:pointer;
}

.search{
    float:right;
    margin:7px;
    width: 800px;
    /*height: -80px;*/
    flex-shrink: 0;
    margin-left: 65px;
    margin-top: 1px;
}
.search-drop-home{
    display: flex;
    flex-direction: column;
}
.drop-home-button-section{
    display:flex;
    flex-direction:row;
    gap:2em;
    margin-top:25px;
    margin-left:150px;
    /* border: 2px solid red; */
}
.home-button{
    width: 120px;
    height: 36px;
    flex-shrink: 0;
    background-color: #FCF4D7;
    border-radius: 20px;
    /*cursor:pointer;*/
    color:black;
    /*text-color: --default-text-color;*/
    cursor: pointer;
}

.home-button:hover{
    background-color: darkorange;
    transition: 0.7s;
    cursor:pointer;
}

.cartamount{
    position:absolute;
    top:-21px;
    right:-4px;
    /*color: var(--primary-color2);*/
    color:black;
    font-size: 1.5rem;
}
.login-cart{
    display:flex;
    flex-direction: row;
}